import { reactive, ref, onBeforeUnmount } from "vue";
import { getAge } from "../../utils/getAge";
import m2 from "../../assets/img/m2.png";
import word from "../../assets/img/word.png";
export default {
  name: "author",

  setup() {
    let age = getAge("2000-10-18");
    let timer = null;
    let showActiveIndex = ref(0);
    const p_text = ref("p_text");

    function setTimer() {
      timer = window.setInterval(() => {
        showActiveIndex.value = showActiveIndex.value == 0 ? 1 : 0;
      }, 4000);
    }

    setTimer();
    onBeforeUnmount(() => {
      // 销毁定时器
      clearInterval(timer);
    }); // 创建图片列表

    const imageList = reactive([{
      path: word,
      index: 0
    }, {
      path: m2,
      index: 1
    }]);

    const move = function (e) {
      if (!p_text.value) {
        return;
      }

      let baseX = Math.floor(window.innerWidth / 20);
      let baseY = Math.floor(window.innerHeight / 20);
      p_text.value.style.transform = `translate(${Math.floor(e.pageX / 10) - baseX}px,${Math.floor(e.pageY / 10) - baseY}px)`;
    };

    const leave = function () {
      if (!p_text.value) {
        return;
      }

      p_text.value.style.transform = "translate(0px,0px)";
    };

    return {
      age,
      imageList,
      showActiveIndex,
      move,
      p_text,
      leave
    };
  }

};